/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Ubuntu Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.no-margin {
  margin: 0;
}

.titulo-pag {
  margin-top: 4rem;
  font-size: 2.4rem;
}

h1, h2, h3 {
  font-family: "Ubuntu Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.boton {
  background-color: #A2BFCF;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #80a8be;
  cursor: pointer;
}

.header {
  height: 7rem;
  background-color: #42546A;
  display: flex;
  align-items: center;
}
.header .logo-header {
  width: 8rem;
}
.header .barra {
  display: flex;
  align-items: center;
}
.header .navegacion a {
  font-size: 1.8rem;
  color: #DDDDDD;
  margin-left: 1.5rem;
}
.header .navegacion a:hover {
  color: #FFFFFF;
  transition: color 0.3s ease;
}

.contenido-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.enlace-formulario {
  color: red;
  font-size: 4rem;
}

.contenedor-formulario {
  width: 900px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.card {
  width: 300px;
  background: white;
  padding: 0.4em;
  border-radius: 6px;
}

.card-image img {
  margin: auto;
  width: 90%;
  aspect-ratio: 3/2;
  object-fit: contain;
}

.card-image:hover {
  transform: scale(0.98);
}

.category {
  text-transform: uppercase;
  font-size: 0.7em;
  font-weight: 600;
  color: rgb(63, 121, 230);
  padding: 10px 7px 0;
}

.category:hover {
  cursor: pointer;
}

.heading {
  font-weight: 600;
  color: rgb(88, 87, 87);
  padding: 7px;
}

.heading:hover {
  cursor: pointer;
}

.author {
  color: gray;
  font-weight: 400;
  font-size: 11px;
  padding-top: 20px;
}

.name {
  font-weight: 600;
}

.name:hover {
  cursor: pointer;
}

.card-details {
  width: 100%;
  display: flex;
}

.bienvenida {
  text-align: center;
}
.bienvenida h2 {
  margin-top: 2rem;
}
.bienvenida p {
  text-align: center;
}

.ofertas-destacadas {
  margin-top: 2rem;
}

.formulario {
  margin-top: 2rem;
  max-width: 1300px; /* Establecer un ancho máximo para el formulario */
  margin-left: auto; /* Centrar el formulario horizontalmente */
  margin-right: auto; /* Centrar el formulario horizontalmente */
  padding: 20px; /* Reducir el padding del formulario */
  border: 1px solid #ccc; /* Agregar un borde al formulario */
  border-radius: 8px; /* Agregar bordes redondeados al formulario */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* Agregar una sombra al formulario */
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: 2px solid #ccc; /* Establece un borde sólido de 2px */
  border-radius: 8px; /* Agrega bordes redondeados */
  padding: 10px; /* Aumenta el relleno para que el texto no esté demasiado cerca del borde */
  background-color: #f9f9f9; /* Cambia el color de fondo */
  transition: border-color 0.3s ease; /* Agrega una transición suave para el cambio de color de borde */
}
.campo input:focus {
  border-color: #007bff; /* Cambia el color del borde cuando el input está enfocado */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Agrega una sombra al input enfocado */
}

.contenedor-tarjetas {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}
.contenedor .aplicaciones-tabla {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}
.contenedor .aplicaciones-tabla th, .contenedor .aplicaciones-tabla td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
.contenedor .aplicaciones-tabla thead {
  background-color: #f2f2f2;
}
.contenedor .aplicaciones-tabla thead th {
  font-weight: bold;
}
.contenedor .aplicaciones-tabla tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
.contenedor .aplicaciones-tabla tbody tr:hover {
  background-color: #f1f1f1;
}